.caja-panel{
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  background-image: url("../img/fondo_alto.jpg");
}

.centro-panel{
  width: 100%;
  height: 60vh;
  background-image: url("../img/fondoproductos.jpg");
  background-size: cover;
  background-position: 10% 30%;
  filter: brightness(0.5)
}

.container-panel{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  min-height: 80vh;
  justify-content: space-evenly;
  
}

.titulo-panel{
  width: 100%;
  text-align: center;
  margin: auto;
  color: #efeefe;
  font-size: 3em;
}


.card-panel{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 60%;
  height: 38vh;
  color: #efeefe;
  border-radius: 5px;
  -webkit-box-shadow: inset -5px 17px 109px 8px rgba(59, 38, 38, 0.88);
