  box-shadow: inset -5px 17px 109px 8px rgba(49, 49, 49, 0.88);
  background-image: url(https://jberlife.com/wp-content/uploads/2021/04/GettyImages-1191995863-2-scaled.jpg);
  margin: 4rem 0 2rem;
}

.panel-imgproducto{
  width: 30%;
  height: 100%; 
  display: flex;
  align-items: center ;
  margin-right: .2rem;
}

.imagen-producto{
  width: 100%;
  height: 90%;
  margin: auto;
  object-fit: scale-down;
  border-right: 3px solid #efeefe;
   
}

 .items-producto{
  display: flex;
  width: 33%;
  flex-direction: column;
  height: 100%;
} 

.subtitulo-producto{
  width: 100%;
  height: 30%;
  padding: .6rem 0;
}

 .respuesta-producto{
   padding-bottom: .5rem;
   border-bottom: 1px solid #efeefe;
   width: 90%;
   font-size: 1.1em;
 }

.boton-panel{
  width: 20%;
  height: 35vh;
  display: flex;
  margin: 4rem 0 2rem;
  display: flex;
  flex-direction: column; 
  justify-content: center;
  
}


.boton-panelProduct{
  border: 2px solid #efeefe;
  font-size: 1.2em;
  padding: 0.3rem;
  margin: 0 5rem ;
  border-radius: 5px;
  background-color: transparent;
  color: #efeefe;
  cursor: pointer;
  transition: all 0.5;
}

.boton-panelProduct:hover {
  transform: scale(1.02, 1.02);
}


.container-butonPanel{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}







/* -----------------botones--------------- */


.button-elimintate {
  width: 150px;
  height: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  background: red;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
  background: #e62222;
  margin: 1rem 0;
}

.button-elimintate,
.button-elimintate span {
  transition: 200ms;
}

.button-elimintate .text {
  transform: translateX(35px);
  color: white;
  font-weight: bold;
}

.button-elimintate .icon {
  position: absolute;
  border-left: 1px solid #c41b1b;
  transform: translateX(110px);
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-elimintate svg {
  width: 15px;
  fill: #eee;
}

.button-elimintate:hover {
  background: #ff3636;
}

.button-elimintate:hover .text {
  color: transparent;
}

.button-elimintate:hover .icon {
  width: 150px;
  border-left: none;
  transform: translateX(0);
}

.button-elimintate:focus {
  outline: none;
}

.button-elimintate:active .icon svg {
  transform: scale(0.8);
}


