/* --------------------------form agregar producto---------------- */

#form-signup-panelProducts {
  margin: 8rem auto;
  width: 50%;
  height: 100%;
  transition: all 1s ease-in-out;
}


.container-panelProduct {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.butonpanelProducts {
  cursor: pointer;
  font-size: 1rem !important;
}



/* form */
@keyframes ingreso {
  0% {
    margin-bottom: -1000px;
    opacity: 0;
  }

  100% {
    margin-bottom: 5rem;
    opacity: 1;
  }
}

.formContainer-panelProducts {
  min-height: 30vh;
  display: flex;
  width: 100%;
  justify-content: center;
  overflow: hidden;
  position: relative;

  /* background-color: #EFEEFE; */

}

.mainForm-panelProducts {
  justify-content: flex-start;
  align-items: center;
  padding-top: 1rem;
  /* background-color: #5c5b5bdc !important; */
  background-color: #efeefed5;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.subtitle-signup-panelProducts {
  padding: 0 0 .2rem 2rem;
  margin-bottom: 1.5rem;
  /* background-color: #EFEEFE; */
  width: 80%;
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  letter-spacing: .5px;
  text-transform: uppercase;
}

.form-panelProducts {
  width: 100%;
  /* background-color: #EFEEFE; */
  border: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: stretch;
  animation: ingreso;
  animation-duration: 1s;
  animation-iteration-count: initial;
  z-index: 5;
}


.fieldsetFrom-panelProducts {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border: none;

}

.span-signup-panelProducts {
  font-weight: bold;
}

.labelForm-panelProducts {
  display: flex;
  flex-direction: column;
  padding: .2rem;
  width: 70%;
  margin-bottom: .3rem;
  margin-top: 1rem;
}

.inputFrom-panelProducts {
  color: black;
  width: 100%;
  align-self: center;
  height: 1.60rem;
  border: none;
  padding-left: 2rem;
  font-weight: 600;
  margin-bottom: .5rem;
  border-bottom: 1px solid black;
  background-color: transparent !important;
}

.inputFrom-panelProducts option {
  color: black;
}


.button-panelProducts {
  font-size: 1rem;
  background: #EFEEFE;
  color: #000;
  padding: 0.5em .5em;
  display: flex;
  align-items: center;
  border: 1px solid black;
  border-radius: 1px;
  overflow: hidden;
  margin: .5rem auto 1.5rem;
  transition: all 0.2s;
}

.button-panelProducts span {
  display: block;
  margin-left: 0.3em;
  transition: all 0.3s ease-in-out;
}

.linkSingIn-panelProducts {
  font-size: .9rem;
  margin-top: .5rem;
}

.button-panelProducts svg {
  display: block;
  transform-origin: center center;
  transition: transform 0.3s ease-in-out;
}

.button-panelProducts:hover .svg-wrapper {
  animation: fly-1 0.6s ease-in-out infinite alternate;
}

.button-panelProducts:hover svg {
  transform: translateX(1.2em) rotate(45deg) scale(1.1);
}

.button-panelProducts:hover span {
  transform: translateX(5em);
}

.button-panelProducts:active {
  transform: scale(0.95);
}

@keyframes fly-1 {
  from {
    transform: translateY(0.1em);
  }

  to {
    transform: translateY(-0.1em);
  }
}


